import Product from '../AllProduct/Product2'
export default {
  name: 'search',
  components: {
    myProduct: Product,
  },
  data () {
    return {
      hotSearch: [],
      isShowHot: true,
      isEmpty: false,
      results: [],
      value: '',
    }
  },
  created () {
    this.doFocusFn();
  },
  mounted () {
    console.log('search===================mounted')
  },
  activated () {
    // this.doFocusFn();
    console.log('search===================activated')
  },
  deactivated () {
    console.log('search===================deactivated')
  },
  destroyed () {
    console.log('search===================destroyed')
  },
  methods: {
    goBackFn() {
      this.$router.go(-1);
    },
    doSearchFn() {    
      if (this.value) 
        comTools.setSearch(this.value);
      comTools.showLoading(this); 
      setTimeout((data) => {
        comTools.hideLoading(this);
        let productList = this.$store.state.allGoods;

        if (Math.random()*10 > 5) 
          productList = [];
        
        if (productList.length > 0) {
          this.isShowHot = false;
          this.isEmpty = false;
          // 生成滚动条
          this.resetHeight();
          this.results = productList;
        } else {
          this.isShowHot = true;
          this.isEmpty = true;
          this.results = productList;
          // 自动获取焦点
          this.doFocusFn();
        }
      }, 500)
    },
    doInputFn () {
      this.value = this.value.replace(',','');
    },
    doFocusFn () {
      this.hotSearch = comTools.getSearch();
      this.isShowHot = true;
    },
    doHotSearchFn (key) {
      this.value = key;
      this.doSearchFn();
    },
    resetHeight() {
      let self = this, dom = this.$refs.searchGoods;
      // 重置高度
      comTools.resetHeight(dom,0.95);
    },
    searchDeleteFn () {
      let self = this
      this.$vux.confirm.show({
        title: '全部删除',
        content: '确定全部删除历史搜索记录吗?',
        onConfirm() {
          comTools.deleteSearch();
          self.doFocusFn();
        }
      });
    }
  }
  
}




