export default {
  name: "product2",
  props:['value'],
  data() {
    return {
      data: {
        NAME: "儿童潜力",
        WEIGHT: 0,
        DETAIL_IMG: "",
        MID_IMG: "",
        ID: "06808bd3d8314c308967e23e00ec6209",
        MARKET_PRICE: 100,
        SMALL_IMG: "",
        DISCOUNT: 10,
        COST_PRICE: 10
      },
      imgUrl:this.$store.state.imgUrl,
    }
  },
  methods: {
    goToDetail () {
      this.$router.push({path:"/goodsdetail/" + this.data.ID});
    }
  },
  created() {
    this.data = this.value;
  }
};



